@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Dancing+Script&family=Rajdhani:wght@500&family=Saira+Condensed:wght@200;500&display=swap);

 .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 22px;
    border: 1px solid #cfcece;
  }
  .close:hover{
    background: red;
    color: white;
  }
  .infoCard{
    box-shadow: 0px 0px 300px 112px rgba(0,0,0,0.75);
  }
.background_img{
    background-image:url(/In-Court/static/media/court.7a8a7667.jpg);
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    background-size: cover;

}
.hero-text {
    background-color: rgba(0, 0, 0, 0.7);
    width: 40%;
    height: 40%;
    text-align: center;
    position: absolute;
    /* top: 50%; */
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    border-radius: 20px;
  }
  .content{
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;

  }

.fcard{
  box-shadow: 0px 10px 0px 10px rgba(0,0,0,0.75);
}
.shade{
  height: 2rem;
  background-image: linear-gradient(0deg,transparent,rgba(0,0,0,081),#111);
}



@media only screen and (max-width: 600px) {
 .hero-text{
   width: 70%;
   height: 50%;
   
 }
 .content h1{
   font-size: 1.6rem;
 }
 .content h3{
   font-size: 1.2rem;
 }
 
}



.nav-link{
    color:white !important;
}
.nav-link:hover{
    color:#5bc0de !important;
    
}

.menu_active{
    border-bottom: 2px solid #5bc0de ;
    font-weight: bolder;
    color: white;
}


@media only screen and (max-width: 600px) {
    h2{
        font-size: 1.2rem;
        
    }
  }
